.home {
  display: flex;
  height: 80rem;
  @include container;
  padding-top: 8rem;

  &__msg {
    display: flex;
    align-items: center;
    width: 51%;

    &--content {
      display: flex;
      flex-direction: column;
      padding: 0 3rem 0 0;
    }

    &--h1 {
    }

    &--h2 {
      padding: 0.2rem 0 2rem 0;
    }

    &--p {
    }

    &--link {
      display: flex;
      align-self: flex-end;
      justify-content: center;
      align-items: center;
      background: $color-primary;
      width: 12rem;
      padding: 1rem;
      margin: 5rem 0 0 0;
      transition: all 0.5s;

      &:hover {
        background: white;
        outline: 1px solid $color-primary;
        color: $color-primary;
      }
    }
  }

  &__img {
    display: flex;
    justify-content: space-between;
    width: 49%;

    &--img {
      &:nth-child(1) {
        width: 15rem;
        height: 65rem;
        background-size: cover !important;
        background-position-x: -4rem !important;
        background-repeat: no-repeat;
      }

      &:nth-child(2) {
        width: 9.5rem;
        margin: 0 0.2rem;

        background-size: cover !important;
        background-position-x: -17rem !important;
        background-repeat: no-repeat;
      }

      &:nth-child(3) {
        width: 9.5rem;
        height: 65rem;

        background-size: cover !important;
        background-position-x: -9rem !important;
        background-repeat: no-repeat;
      }

      &:nth-child(4) {
        width: 9.5rem;
        margin: 7rem 0.2rem 0;
        height: 60rem;

        background-size: cover !important;
        background-position-x: -15rem !important;
        background-repeat: no-repeat;
      }

      &:nth-child(5) {
        width: 15rem;

        background-size: cover !important;
        background-position-x: -10rem !important;
        background-repeat: no-repeat;
      }
    }
  }
}
